import PropTypes from 'prop-types';
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import faYoutube from '@fortawesome/fontawesome-free-brands/faYoutube'
import faFacebook from '@fortawesome/fontawesome-free-brands/faFacebook'
import faInstagram from '@fortawesome/fontawesome-free-brands/faInstagram'
import Link from 'next/link';

class Main extends React.Component {
  render() {

    let close = <div className="close" onClick={() => {this.props.onCloseArticle()}}></div>

    return (
      <div id="main" style={this.props.timeout ? {display: 'flex'} : {display: 'none'}}>

        <article id="intro" className={`${this.props.article === 'intro' ? 'active' : ''} ${this.props.articleTimeout ? 'timeout' : ''}`} style={{display:'none'}}>
          <h2 className="major">Intro</h2>
          <span className="image main"><img src="/static/images/pic01.jpg" alt="" /></span>
          <p>
            Chaotic 3 is the <strong>second best</strong> dance team based at the University of Southern California.
            Originally founded as an artistic outlet for members of the Chinese American Student Association,
            Chaotic 3 has evolved into a well-known team that strives to foster the passion for dance from new and veteran dancers alike.
            Through exhibition and competition performances on USC's campus and around Southern California such as Maxt Out, Prelude aka The Bridge, Urban Street Jam and All Cal,
            they hope to continue to be influenced by the best while maintaining the spirit that is Chaotic 3.
          </p>
          {close}
        </article>

        <article id="awards" className={`${this.props.article === 'awards' ? 'active' : ''} ${this.props.articleTimeout ? 'timeout' : ''}`} style={{display:'none'}}>
          <h2 className="major">Awards</h2>
          <span className="image main"><img src="./../static/images/pic02.jpg" alt="" /></span>
          <ul>
            <li>USC's Best Dance Crew 2019 - 2nd Place</li>
            <li>Maxt Out 2018 - 2nd Place</li>
            <li>Urban Street Jam 2018 - 1st Place</li>
            <li>Urban Street Jam 2016 - 3rd Place</li>
            <li>Ulimate Brawl 2015 - 3rd Place</li>
          </ul>
          {close}
        </article>

        <article id="audition" className={`${this.props.article === 'audition' ? 'active' : ''} ${this.props.articleTimeout ? 'timeout' : ''}`} style={{display:'none'}}>
          <h2 className="major">Audition</h2>
          <span className="image main"><img src="/static/images/pic03.jpg" alt="" /></span>
          <p>We're glad that you're excited about joining the team! To sign up for auditions or view your audition results, click on this <Link href="/audition"><a>link</a></Link>.</p>
          {close}
        </article>

        <article id="contact" className={`${this.props.article === 'contact' ? 'active' : ''} ${this.props.articleTimeout ? 'timeout' : ''}`} style={{display:'none'}}>
          <h2 className="major">Contact</h2>
          <form method="post" action="#">
            <div className="field half first">
              <label htmlFor="name">Name</label>
              <input type="text" name="name" id="name" />
            </div>
            <div className="field half">
              <label htmlFor="email">Email</label>
              <input type="text" name="email" id="email" />
            </div>
            <div className="field">
              <label htmlFor="message">Message</label>
              <textarea name="message" id="message" rows="4"></textarea>
            </div>
            <ul className="actions">
              <li><input type="submit" value="Send Message" className="special" /></li>
              <li><input type="reset" value="Reset" /></li>
            </ul>
          </form>
          <ul className="icons">
            <li><a href="https://www.facebook.com/chaoticthree/">
              <FontAwesomeIcon icon={faFacebook} />
            </a></li>
            <li><a href="https://www.instagram.com/chaoticthree/">
              <FontAwesomeIcon icon={faInstagram} />
            </a></li>
            <li><a href="https://www.youtube.com/user/chaoticthree">
              <FontAwesomeIcon icon={faYoutube} />
            </a></li>
          </ul>
          {close}
        </article>

      </div>
    )
  }
}

Main.propTypes = {
  route: PropTypes.object,
  article: PropTypes.string,
  articleTimeout: PropTypes.bool,
  onCloseArticle: PropTypes.func,
  timeout: PropTypes.bool
}

export default Main